@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.msg-left,.msg-right,.msg-price{font-size:.75rem;max-width:70%;border-radius:1.125rem;padding:1rem;margin-bottom:.5rem}.msg-left,.msg-price{border-end-start-radius:0.1rem}.msg-left{background-color:#fbf5ff;background-color:#f4e8fd;color:#9241c8}.msg-right{border-end-end-radius:0.1rem;background-color:white;color:#6e5d7e;margin-left:auto}.msg-price{background-image:linear-gradient(to right, #e942ff, #8838ff);color:#d89eff;max-width:80%}.msg-price-check{background-color:transparent !important;border:#e570ff 1px solid !important;border-radius:100% !important}.msg-price-price{color:#fff}.msg-images{display:flex;margin-left:auto}.msg-images-img{width:4rem;border-radius:1rem;margin-bottom:.5rem}.msg-images-img:not(:last-child){margin-right:.5rem}.msg-images-img:first-child{margin-left:auto}

.phone{background-color:#f5f3f7;position:relative;display:flex;flex-direction:column;min-width:354px;max-width:354px;word-wrap:break-word;background-clip:border-box;border:1rem solid #fff;margin:0 auto;border-radius:3rem}@media (min-width: 992px){.phone{margin-right:0}}.phone-header{border-radius:.75rem;border-start-end-radius:2.5rem;border-start-start-radius:2.5rem;color:#fff;display:flex;padding:2.75rem 1rem 1rem;margin-bottom:0;background-image:linear-gradient(to right, #8838ff, #e942ff);border-bottom:1px solid rgba(0,0,0,0.125)}.phone-header p{color:#d89eff;font-size:.75rem}.phone-header:before{content:"";border-end-end-radius:1.25rem;border-end-start-radius:1.25rem;position:absolute;top:0;left:50%;height:1.5rem;width:50%;background-color:#fff;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.phone-header .avatar{width:3rem;height:3rem;border-radius:100%;border:2px solid white}.phone-header .title{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.phone-header .title h6{margin-bottom:.125rem}.phone-header .title p{margin-bottom:0}.phone-body{min-height:20rem;flex:1 1 auto;padding:0.75rem}.phone-body .input-group-text{padding:0.375rem}.phone-body .msg{border-right:none;padding-left:1rem}.phone-body .msg::-webkit-input-placeholder{color:#c6cacd}.phone-body .msg:-ms-input-placeholder{color:#c6cacd}.phone-body .msg::placeholder{color:#c6cacd}.phone-body .msg-send-btn{height:1.8rem;width:1.8rem;display:flex;align-items:center;justify-content:center;background-color:#3e2753}

.App{background-color:#fff;font-family:"Rubik",sans-serif;font-size:400;position:relative}.App::before{content:"";position:absolute;bottom:0;right:-10%;top:60%;left:60%;background-color:rgba(244,232,253,0.3);border-start-start-radius:100% 60%}@media (min-width: 992px){.App::before{top:20%;left:85%;border-start-end-radius:100% 60%}}.App .dot{background-color:#d89eff;height:.25rem;width:.25rem;margin-bottom:.125rem}.App .phone-col{position:relative;padding:5rem 0}.App .phone-col:before{content:"";position:absolute;top:0;left:0;height:85%;width:50%;background-image:linear-gradient(#e942ff, #8838ff);border-end-end-radius:100% 40%}@media (min-width: 992px){.App .phone-col:before{border-end-end-radius:100% 50%;border-end-start-radius:100% 50%;margin-left:-6rem;width:70%}}.App .text-col{text-align:center;margin:0 auto;padding:1rem}@media (min-width: 992px){.App .text-col{text-align:start;padding:0 0 0 7rem}}.App .text-col h1{color:#3e2753;font-size:2.65rem;margin-bottom:2rem}.App .text-col p{color:#a39daa;font-weight:500;line-height:1.755rem;max-width:342px;margin:0 auto;z-index:1}@media (min-width: 992px){.App .text-col p{margin:0;max-width:75%}}.App .radius{border-radius:0.75rem}.App .round{border-radius:6.25rem}.fade-in{opacity:0;-webkit-animation:fadeIn ease 5s;animation:fadeIn ease 5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.delay-2{-webkit-animation-delay:2s;animation-delay:2s}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

