// # Front-end Style Guide

// ## Layout

// The designs were created to the following widths:

// - Mobile: 375px
// - Desktop: 1440px

// ## Colors

// ### Primary

// #### Text

// - Pale Violet (sub-heading at the top of the app UI):
$pale-violet: hsl(276, 100%, 81%);
$very-pale-violet: hsl(276, 100%, 98%);
// - Moderate Violet (chat on the left):
$moderate-violet: hsl(276, 55%, 52%);
$moderate-violet-desat: hsl(276, 82%, 95%);
// - Desaturated Dark Violet (chat on the right)
$dark-violet: hsl(271, 15%, 43%);
// - Grayish Blue (placeholder text)
$grayish-blue: hsl(206, 6%, 79%);
// - Very Dark Desaturated Violet (main heading)
$very-dark-violet: hsl(271, 36%, 24%);
// - Dark Grayish Violet (paragraph)
$dark-grayish-violet: hsl(270, 7%, 64%);

// #### Gradients

// These two colors are the vibrant background colors you see throughout the design and are applied as a linear gradient:

// - Light Magenta
$light-magenta: hsl(293, 100%, 63%);
// - Light Violet
$light-violet: hsl(264, 100%, 61%);

$gradient-background-top: linear-gradient( $light-magenta, $light-violet);
$gradient-background-header: linear-gradient(to right, $light-violet, $light-magenta);
$gradient-background-msg: linear-gradient(
  to right,
  $light-magenta,
  $light-violet
);

// ### Secondary

// - White
$white: hsl(0, 0%, 100%);
// - Light Grayish Violet (app background)
$light-grayish-violet: hsl(270, 20%, 96%);
// - Very Dark Desaturated Violet (submit button background)
// $very-dark-violet: hsl(271, 36%, 24%);
// - Very Light Magenta (radio button outline)
$very-dark-magenta: hsl(289, 100%, 72%);

// ## Typography

// ### Body Copy

// - Font size
$default-font-size: 16px;

// ### Font

// - Family: [Rubik](https://fonts.google.com/specimen/Rubik)
// - Weights: 400, 500, 700
$default-font-family: "Rubik", sans-serif;